<template>
    <div>
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>分类</span>
            </div>
            <div
                class="group-item"
                :class="updown == 'down' ? 'active' : ''"
                @click="updownClick('down')"
            >
                <div>
                    <i class="fa fa-arrow-down m-r-5 text-green"></i>
                    Down
                </div>
            </div>
            <div
                class="group-item"
                :class="updown == 'up' ? 'active' : ''"
                @click="updownClick('up')"
            >
                <div>
                    <i class="fa fa-arrow-up m-r-5 text-red"></i>
                    Up
                </div>
            </div>
        </el-card>
        <el-card style="margin-top: 20px" class="box-card">
            <div
                class="group-item"
                :class="onoff == 'on' ? 'active' : ''"
                @click="onoffClick('on')"
            >
                <div>
                    <i class="fa fa-play m-r-5 text-green"></i>
                    On
                </div>
            </div>
            <div
                class="group-item"
                :class="onoff == 'off' ? 'active' : ''"
                @click="onoffClick('off')"
            >
                <div>
                    <i class="fa fa-pause m-r-5 text-red"></i>
                    Off
                </div>
            </div>
        </el-card>
    </div>
</template>

<script>
export default {
    name: "fogtype",
    props: {},
    data() {
        return {
            seltype: "",
            updown: "",
            onoff: "",
            updownSelected: false,
            onoffSelected: false,
        };
    },
    methods: {
        updownClick(item) {
            if (this.updown == item) {
                this.updownSelected = !this.updownSelected;
            }
            if (this.updownSelected) {
                this.updown = "";
                this.updownSelected = false;
            } else {
                this.updown = item;
            }
            this.$emit("callType", item); //将值传给父组件
        },
        onoffClick(item) {
            if (this.onoff == item) {
                this.onoffSelected = !this.onoffSelected;
            }
            if (this.onoffSelected) {
                this.onoff = "";
                this.onoffSelected = false;
            } else {
                this.onoff = item;
            }
            this.$emit("callType", item); //将值传给父组件
        },

        // removeSelected() {
        //     this.seltype = "";
        //     this.$emit("callType", {});
        // },
    },
};
</script>
<style scoped>
.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}
.clearfix:after {
    clear: both;
}

.box-card .item.active {
    background: #ebf5ff;
}

.box-card .item:hover {
    background: #ebf5ff;
}
.text-red {
    color: #ff3e12;
}
.text-green {
    color: #40a72c;
}
</style>


